/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin, padding and set font inherit to all elements */
* {
  padding: 0;
  margin: 0;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

/* Set core body defaults */
body {
  text-rendering: optimizeSpeed;
  line-height: 1;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Remove default underline from A elements */
a,
a:visited,
a:active {
  text-decoration: none;
}

/* Remove list styles on ul, ol elements */
ul,
ol {
  list-style: none;
}

/* Make images easier to work with */
img,
picture,
svg {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Set core buttons and inputs defaults */
button,
[type=button],
[type=reset],
[type=submit] {
  border: 0;
  cursor: pointer;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.design {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  opacity: 0.2;
  pointer-events: none;
}

:root {
  --radius-1: 0.5rem;
  --border-0: 0.063rem solid var(--color-gray-1);
  --border-1: 0.063rem solid var(--color-gray-2);
}

body {
  background-color: var(--color-white-8);
}

.container {
  --container-padding-inline: 1.5rem;
  display: flex;
  padding-inline: var(--container-padding-inline);
}
.container--header {
  --container-height: 18.75rem;
  --container-padding-block-start: 2rem;
  justify-content: center;
  height: var(--container-height);
  background: url("../images/image-hero-mobile.jpg") center/cover no-repeat;
  padding-block-start: var(--container-padding-block-start);
}
.container--main {
  --container-row-gap: 1.5rem;
  flex-direction: column;
  align-items: center;
  row-gap: var(--container-row-gap);
}

:root {
  --font-commissioner: "Commissioner", sans-serif;
  --font-size-1: 1rem;
  --font-size-2: 1.125rem;
  --font-size-3: 1.25rem;
  --font-size-4: 1.5rem;
  --font-size-5: 2rem;
  --font-size-6: 2.5rem;
  --font-weight-4: 400;
  --font-weight-5: 500;
  --font-weight-7: 700;
}

body {
  font-family: var(--font-commissioner);
  font-size: var(--font-size-1);
}

:root {
  --color-cyan-6: hsl(176, 50%, 47%);
  --color-cyan-8: hsl(176, 72%, 28%);
  --color-black: hsl(0, 0%, 0%);
  --color-gray-1: hsl(0, 0%, 48%, 0.1);
  --color-gray-2: hsl(0, 0%, 48%, 0.3);
  --color-gray-5: hsl(0, 0%, 48%);
  --color-white-5: hsl(0, 0%, 100%);
  --color-white-8: hsl(0, 0%, 98%);
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
}
.button--menu {
  --button-padding: 0.25rem;
  background-color: transparent;
  padding: var(--button-padding);
  margin-inline-end: calc(var(--button-padding) * -1);
}
.button--menu .icon--close {
  display: none;
}
.button--menu.active .icon--hamburger {
  display: none;
}
.button--menu.active .icon--close {
  display: block;
}

.section {
  --section-padding-inline: 1.375rem;
  --section-padding-block-end: 2.375rem;
  position: relative;
  background-color: var(--color-white-5);
  border: var(--border-0);
  border-radius: var(--radius-1);
  padding-inline: var(--section-padding-inline);
  padding-block-end: var(--section-padding-block-end);
}

.nav {
  --nav-height: 1.25rem;
  --nav-max-width: 69.25rem;
  width: min(100%, var(--nav-max-width));
  height: var(--nav-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.nav__menu {
  --menu-max-width: 30rem;
  width: min(100%, var(--menu-max-width));
  position: absolute;
  top: calc(100% + 2.25rem);
  right: 0;
  visibility: hidden;
  background-color: var(--color-white-5);
  border-radius: var(--radius-1);
}
.nav__menu .menu__list {
  display: flex;
  flex-direction: column;
}
.nav__menu .menu__item:nth-of-type(n + 2) {
  border-block-start: var(--border-1);
}
.nav__menu .menu__link {
  --link-height: 4.375rem;
  --link-padding-inline-start: 1.5rem;
  height: var(--link-height);
  display: flex;
  align-items: center;
  padding-inline-start: var(--link-padding-inline-start);
  font-size: var(--font-size-2);
  font-weight: var(--font-weight-5);
  color: var(--color-black);
}